@use './normalize.css';

@mixin max-mobile-width() {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin min-tablet-width() {
  @media screen and (min-width: 768px) {
    @content;
  }
}

@mixin min-desktop-width() {
  @media screen and (min-width: 960px) {
    @content;
  }
}

@mixin min-xl-width() {
  @media screen and (min-width: 1100px) {
    @content;
  }
}

/**************************
        GENERAL
***************************/
body {
  font-family: 'Lato', sans-serif !important;
  height:100vh; margin:0; 
  display:flex; 
  flex-direction:column; 
}

.bold {
  font-weight: bold;
}

.italic {
  font-style: italic;
}

.centerimg {
  box-sizing: border-box;
  padding: 10px;
}

.clearfix:after {
  content: ' ';
  display: table;
  clear: both;
}

/**************************
        HEADER
***************************/
header {
  
  + div,
  + article {
    padding-top: 100px;
  }

  #logo {
    text-align: center;
    @include min-tablet-width() {
      float: left;
      width: 40%;
      margin-left: 5%;
      text-align: left;
    }
  }

  h1 {
    color: white;
    font-family: 'Lato', bold;
    margin: 5px 0 3px 0;
    font-size: 1.5rem;
  }

  h2 {
    color: #ffffff;
    font-family: 'Lato', bold;
    margin: 2px 0 4px 0;
    font-size: 1.1rem;
  }

  h3 {
    text-align: center;
  }

  nav {
    font-family: 'Lato', bold;
    background-color: rgb(132, 172, 211);
    opacity: 0.7;
    text-align: center;
    padding: 5px 0 5px 0;

    a {
      color: #fff;
      font-weight: bold;
      padding: 0 2px;
      transition: all 0.2s;

      &:hover {
        color: #1b82d1;
        text-decoration: none;
      }
    }

    @include min-tablet-width() {
      float: right;
      width: 49%;
      margin-right: 3%;
      background: none;
      text-align: right;
      padding-top: 15px;
      opacity: 1;

      a {
        padding: 0 5px;
        font-weight: bold;
        font-size: 1.1em;
      }
    }

    ul {
      list-style: none;
      margin: 5px 0;
      padding: 0;
    }
    li {
      display: inline-block;
    }
  }

  &.main-header {
    background-color: rgba(2, 40, 115, 0.8);
    margin: 0 0 0 0;
    padding: 5px 0 0 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;

    @include min-tablet-width() {
      box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.4);
    }

    @include max-mobile-width() {
      h1 {
        font-size: 1.4em;
        padding-bottom: 0.3em;
      }

      h2 {
        display: none;
      }
    }
  }
}

/**************************
        FOOTER
***************************/
.main-footer {
  margin-top:auto; 
  padding: 25px;
  background-color: rgba(102, 180, 239, 0.5);
  text-align: center;
  clear: both;

  p {
    font-size: 0.85em;
    margin-bottom: 0;
  }

  a {
    color: inherit;
  }

  .icona_social {
    width: 35px;
    height: 35px;
    margin: 0 5px;
  }
}
